@import url('https://fonts.googleapis.com/css?family=Josefin+Sans');

body{
  font-family:'Open Sans', sans-serif;
  background-color:#f4f4f4;
  overflow-x:hidden;
  height:100%;
}
h1, h2, h5{
  font-weight:200;
  color:rgba(225,225,225,1);
  font-family:'Josefin Sans';
}

.header{
  background-color:rgba(25,25,25,1);
  overflow:hidden;
  height:55px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction:row;
      flex-direction:row;
  -ms-flex-pack:justify;
      justify-content:space-between;
  -ms-flex-align:center;
      align-items:center;
  width: 100%;
  top: 0;
  margin-top: 0;
  z-index: 2;

}

.header a{
  float:left;
  display:block;
  color:#f2f2f2;
  text-decoration:none;
  font-size:17px;
  margin-left:20px;
  cursor:pointer;
  
}

.header ul{
  list-style:none;
  padding:0;
}

header h1{
  padding-right:1em;
}
/* ====================== SIDEBAR ==================== */
.side-nav{
  height:100%;
  width:280px;
  position:fixed;
  z-index:1;
  top:0;
  left:0;
  background:
  -webkit-gradient(
    linear, left top, left bottom, from(rgba(42,42, 42, 1)), to(rgba(48, 48, 48, 1))
  );
  background:
  -webkit-linear-gradient(
    rgba(42,42, 42, 1), rgba(48, 48, 48, 1)
  );
  background:
  -o-linear-gradient(
    rgba(42,42, 42, 1), rgba(48, 48, 48, 1)
  );
  background:
  linear-gradient(
    rgba(42,42, 42, 1), rgba(48, 48, 48, 1)
  );
  overflow-x:hidden;
  padding-top:7px;
  -webkit-transition:0.5s;
  -o-transition:0.5s;
  transition:0.5s;
  -webkit-box-shadow: 1px 0 12px 1px rgba(0,0,0,0.6);
          box-shadow: 1px 0 12px 1px rgba(0,0,0,0.6);
}
.side-nav ul{
  padding:0;
}

.side-nav li{
  padding:16px 10px 16px 0;
  text-decoration:none;
  display:block;
  -webkit-transition:0.3s;
  -o-transition:0.3s;
  transition:0.3s;
  margin-bottom:1px;
  font-size:13px;
  color:rgb(225, 225, 225);
  cursor:pointer;
  background:
  -webkit-gradient(
    linear, left top, left bottom, from(rgba(66,66, 66, 1)), to(rgba(56, 56, 56, 1))
  );
  background:
  -webkit-linear-gradient(
    rgba(66,66, 66, 1), rgba(56, 56, 56, 1)
  );
  background:
  -o-linear-gradient(
    rgba(66,66, 66, 1), rgba(56, 56, 56, 1)
  );
  background:
  linear-gradient(
    rgba(66,66, 66, 1), rgba(56, 56, 56, 1)
  );
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
@media screen and(min-width:1500px){
  .side-nav li{
    padding:17px 10px 17px 0;
  }
  }

.box{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-align: center;
      align-items: center;
  width:100%;

}

.row{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width:205px;

}
.icon-awesome{
  font-size:1.5em;
  width:20px;
  padding-right:20px;
}
.icon-right{
  font-size:1.2em;
  padding-right:5px;
  padding-top:2px;
}
.side-nav p{
  margin:0;
}
.side-nav h5{
  padding:15px 10px 15px 20px;
  margin:0;
}
.side-nav li:hover{
  color:rgb(25, 198, 255);
}
.list:first-child{
  margin-top:0;
}
.menu-box{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  padding-bottom:5px;
}
.times{
  color:rgb(225, 225, 225);
  padding-right:10px;
  cursor:pointer;
}
.angle-right{
  color:rgba(65, 65, 65, 1) !important;
  font-size: 1.8em !important;
}
.angle-right:hover{
  color:rgb(80, 80, 80) !important; 
}

/* =================== TOOLTIP ======================== */

.tooltip {
  position: relative;
  display: inline-block;
}

.tooltiptext {
  visibility: hidden;
  width: 120px;
  background-color: rgba(0,0,0,0.5);
  color: rgb(225, 225, 225);
  text-align: center;
  border-radius: 3px;
  padding: 8px 2px;
  font-size:11px;

  /* Position the tooltip */
  position: absolute;
  z-index: 1;
  top: -5px;
  right: 120%;
}
.position{
  top: 2px;
  left: 120%; 
}

.tooltip:hover .tooltiptext {
  visibility: visible;
}

.places{
  margin-top:1em;
  border-top:1px solid rgba(27,27,27,1);
  -webkit-box-shadow: 0 1px 1px 0 rgba(35,35,35,1);
          box-shadow: 0 1px 1px 0 rgba(35,35,35,1);
}


input::-webkit-input-placeholder {
  padding-left:20px;
  color:rgb(150, 150, 150);
}


input:-ms-input-placeholder {
  padding-left:20px;
  color:rgb(150, 150, 150);
}


input::-ms-input-placeholder {
  padding-left:20px;
  color:rgb(150, 150, 150);
}


input::placeholder {
  padding-left:20px;
  color:rgb(150, 150, 150);
}

.searchbox{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align:center;
      align-items:center;
}

.search{
  width:100%;
  height:40px;
  background-color:rgba(30,30, 30, 1);
  border:none;
  color:white;
}
@media screen and(max-width:768px){
  .header-nav{display:none}
}
/* --------------- Responsive classes ---------------- */
.response{
  -webkit-transform: translateX(-280px);
      -ms-transform: translateX(-280px);
          transform: translateX(-280px);
  }
.active{
  background:transparent !important;

  border-top: 0 !important;
  -webkit-box-shadow: inset 0 7px 9px -7px rgba(25,25,25,1);
          box-shadow: inset 0 7px 9px -7px rgba(25,25,25,1);
  border-left:4px solid rgb(232, 160, 40);
}

/* =============== MAP CONTAINER =================== */

.logo-container{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack: center;
      justify-content: center;
  padding-top:0.5em;
}
.infoWindow{
  width:200px;
}
img{
height:40px;
width:230px;

}


